#wrap {
  width: 200px;
  margin: 0 auto;
  margin-top: 100px;
  margin-bottom: 200px;
}

.App {
  width: 200px;
  height: 200px;
}

.App .node, .App-additional .node {
  position: relative;
}

.App .box {
  width: 100px;
  height: 100px;
  float: left;
  text-align: center;
  line-height: 100px;
  position: relative;
}

.App .first .box {
  border-top-left-radius: 100%;
  background: #FCBFFF;
}

.App .second .box {
  border-top-right-radius: 100%;
  background: #E56B74;
}

.App .third .box {
  border-bottom-left-radius: 100%;
  background: #F9EC93;
}

.App .fourth .box {
  border-bottom-right-radius: 100%;
  background: #85D1B8;
}

.edit {
  width: 36px;
  height: 36px;
  position: absolute;
  background-color: #97ffa0;
  border-radius: 100%;
}

.edit .icon {
  width: 36px;
  height: 36px;
  background-size: 36px 36px;
  position: absolute;
  cursor: pointer;
  -webkit-transition-duration: 500ms;
       -o-transition-duration: 500ms;
          transition-duration: 500ms;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);  
}

.edit .icon.pen {
  background-image: url(data:image/png;base64,);
  z-index: 100;
}

.edit .icon.trash {
  background-image: url(data:image/png;base64,);
  z-index: 99;
}

.edit .icon.trash.active {
  -webkit-transform: translateX(40px);
      -ms-transform: translateX(40px);
          transform: translateX(40px);  
}

.edit .icon.camera {
  background-image: url(data:image/png;base64,);
  z-index: 98;
}

.edit .icon.camera.active {
  -webkit-transform: translateX(-40px);
      -ms-transform: translateX(-40px);
          transform: translateX(-40px);  
}

canvas {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}


#another-wrap {
  width: 600px;
  margin: 0 auto;
  margin-top: 200px;
  margin-bottom: 200px;
}

.App-additional {
  width: 600px;
  height: 200px;
}

.App-additional .griffel .box {
  background-color: rgb(75, 75, 75);
}
.App-additional .hagrid .box {
  background-color: #85D1B8;
}
.App-additional .delacour .box {
  background-color: #E56B74;
}
.App-additional .malfidus .box {
  background-color: #F9EC93;
}
.App-additional .potter .box {
  background-color: #FCBFFF;
}

.App-additional .tops .box {
  background-color: #97ffa0;
}

.App-additional .box {
  width: 200px;
  height: 100px;
  float: left;
  text-align: center;
  line-height: 100px;
  position: relative;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



